/* リキッドレイアウト対応 */
@use 'global' as *;

@if $startFrom == sp {
  //スマホファースト
  html {
    //~375px
    @media (max-width: 375px) {
      font-size: vw(375, 16);
    }

    //375px~767px
    font-size: 16px;

    @include mq('md') {
      font-size: vw(strip-unit($inner), 16);
    }

    //inner~max-screen
    @media (min-width: $inner) {
      font-size: 16px;
    }
  }
} @else {
  //PCファースト
  html {
    font-size: 16px;

    @media (max-width: $inner) {
      font-size: vw(strip-unit($inner), 16);
    }

    @include mq('md') {
      font-size: 16px;
    }

    //~375px
    @media (max-width: 375px) {
      font-size: vw(375, 16);
    }
  }
}

/* ===============================================
# 追記
=============================================== */
a {
  -webkit-tap-highlight-color: transparent; /* 強調をなくす */
}

html {
  scroll-behavior: smooth;
  scroll-padding-top: rem(184);
  @include mq('md') {
    scroll-padding-top: rem(224);
  }
}

body.no-scroll {
  overflow: hidden;
}

body {
  background-color: $base;
}

body.error404 {
  background-color: $font;
}
