  align-items: center;
  display: flex;
  justify-content: center;
  left: 50%;
  max-width: 68.75rem;
  position: absolute;
  top: 50%;
  transform: translate(-50%, -50%);
  width: 90%;
  z-index: 10;
}

.modal__img {
  width: 100%;
}

.modal__img img {
  height: 100%;
  max-height: 90vh;
  object-fit: contain;
  width: 100%;
}

.modal__bg {
  background: rgba(32, 32, 32, 0.8);
  height: 100vh;
  width: 100vw;
}

/* Animation */
.fadeIn-modal {
  animation-name: fadeInModal;
  animation-duration: 0.5s;
  animation-fill-mode: forwards;
}

@keyframes fadeInModal {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}
.fadeOut-modal {
  animation-name: fadeOutModal;
  animation-duration: 0.5s;
  animation-fill-mode: forwards;
}

@keyframes fadeOutModal {
  from {
    opacity: 1;
  }
  to {
    opacity: 0;
  }
}
